// default-footer.scss
@import '../Footer.scss';
#index-footer {
  display: inherit; //~~| display: grid; |~~//
  grid-area: 3 / 1 / 4 / 4; //~~| row-start / col-start / row-end / col-end |~~//

  //--| Desktop (Landscape) -  |--//
  @media screen and (orientation: landscape) and (min-aspect-ratio: 16/9) {
    //--| display: grid; |--//
    grid-template: 4rem / 3rem 50% repeat(3, 1fr) 3rem; //~~| rows / columns |~~//
    .footer-linkedin {
      grid-column: 3 / 4; //~~| col-start / col-end |~~//
    }
    .footer-github {
      grid-column: 4 / 5; //~~| col-start / col-end |~~//
    }
    .footer-youtube {
      grid-column: 5 / 6; //~~| col-start / col-end |~~//
    }

    background: rgba($red-dark, 0.75);
    //~~| border: 4px dashed $red-neon; |~~//
    //~~| background: rgba($red-dark, 0.75); |~~//
  }
  //--| Mobile (Portrait) -  |--//
  @media screen and (orientation: portrait) and (max-aspect-ratio: 1/1) {
    //--| display: grid; |--//
    grid-template: 6rem / repeat(4, 1fr); //~~| rows / columns |~~//
    .footer-home {
      grid-column: 1 / 2; //~~| col-start / col-end |~~//
    }
    .footer-skills {
      grid-column: 2 / 3; //~~| col-start / col-end |~~//
    }
    .footer-contact {
      grid-column: 3 / 4; //~~| col-start / col-end |~~//
    }
    .footer-projects {
      grid-column: 4 / 5; //~~| col-start / col-end |~~//
    }

    background: rgba($blue-dark, 0.75);
    //~~| border: 4px dashed $blue-neon; |~~//
    //~~| background: rgba($blue-dark, 0.75); |~~//
  }
  //--| Tablet (Square) -  |--//
  @media screen and (max-aspect-ratio: 16/9) and (min-aspect-ratio: 1/1) {
    //--| display: grid; |--//

    grid-template: 4rem / 2rem repeat(3, 1fr) 2rem; //~~| rows / columns |~~//
    .footer-home {
      grid-column: 2 / 3; //~~| col-start / col-end |~~//
    }
    .footer-skills {
      grid-column: 3 / 4; //~~| col-start / col-end |~~//
    }
    .footer-contact {
      grid-column: 4 / 5; //~~| col-start / col-end |~~//
    }

    background: rgba($green-dark, 0.75);
    //~~| border: 4px dashed $green-neon; |~~//
    //~~| background: rgba($green-dark, 0.75); |~~//
  }

  a[class*='footer'],
  button[class*='footer'] {
    grid-row: 1 / 2; //~~| row-start / row-end |~~//
  }
}

